@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;900&family=Rajdhani:wght@300;400;500;600;700&display=swap');

:root {
  --neon-blue: #00e6ff;
  --neon-purple: #c400ff;
  --neon-green: #00ff84;
  --neon-pink: #ff0080;
  --neon-orange: #ff6600;
  --dark-bg: #0a0a0a;
  --dark-card: #111111;
  --dark-border: #1a1a1a;
  --futuristic-font: 'Orbitron', monospace;
  --body-font: 'Rajdhani', sans-serif;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background: radial-gradient(ellipse at center, #0f0f23 0%, #0a0a0a 100%) !important;
  color: #e0e0e0 !important;
  font-family: var(--body-font) !important;
  font-weight: 400;
  line-height: 1.6;
  overflow-x: hidden;
}

/* Futuristic Animations */
@keyframes neonPulse {
  0%, 100% {
    box-shadow: 0 0 5px var(--neon-blue), 0 0 10px var(--neon-blue), 0 0 15px var(--neon-blue);
  }
  50% {
    box-shadow: 0 0 10px var(--neon-blue), 0 0 20px var(--neon-blue), 0 0 30px var(--neon-blue);
  }
}

@keyframes neonGlow {
  0%, 100% {
    text-shadow: 0 0 5px var(--neon-green), 0 0 10px var(--neon-green);
  }
  50% {
    text-shadow: 0 0 10px var(--neon-green), 0 0 20px var(--neon-green), 0 0 30px var(--neon-green);
  }
}

@keyframes hologramFlicker {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

@keyframes dataStream {
  0% { transform: translateY(100%); opacity: 0; }
  50% { opacity: 1; }
  100% { transform: translateY(-100%); opacity: 0; }
}

@keyframes circuitPulse {
  0%, 100% {
    background: linear-gradient(90deg, transparent 0%, var(--neon-blue) 50%, transparent 100%);
  }
  50% {
    background: linear-gradient(90deg, transparent 0%, var(--neon-purple) 50%, transparent 100%);
  }
}

/* Custom Components */
.futuristic-card {
  background: rgba(17, 17, 17, 0.9) !important;
  border: 1px solid rgba(0, 230, 255, 0.3) !important;
  border-radius: 12px !important;
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
}

.futuristic-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--neon-blue), transparent);
  animation: circuitPulse 3s infinite;
}

.neon-button {
  background: linear-gradient(135deg, rgba(0, 230, 255, 0.1), rgba(196, 0, 255, 0.1)) !important;
  border: 1px solid var(--neon-blue) !important;
  color: var(--neon-blue) !important;
  font-family: var(--futuristic-font) !important;
  font-weight: 600 !important;
  text-transform: uppercase !important;
  letter-spacing: 1px !important;
  transition: all 0.3s ease !important;
  position: relative;
  overflow: hidden;
}

.neon-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 230, 255, 0.2), transparent);
  transition: left 0.5s;
}

.neon-button:hover::before {
  left: 100%;
}

.neon-button:hover {
  background: linear-gradient(135deg, rgba(0, 230, 255, 0.2), rgba(196, 0, 255, 0.2)) !important;
  box-shadow: 0 0 20px rgba(0, 230, 255, 0.5) !important;
  transform: translateY(-2px) !important;
}

.neon-button:active {
  transform: translateY(0) !important;
}

.neon-button.active {
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-purple)) !important;
  color: #000 !important;
  box-shadow: 0 0 30px rgba(0, 230, 255, 0.8) !important;
}

.neon-text {
  font-family: var(--futuristic-font) !important;
  color: var(--neon-blue) !important;
  text-shadow: 0 0 10px var(--neon-blue) !important;
  animation: neonGlow 2s ease-in-out infinite alternate;
}

.hologram-effect {
  animation: hologramFlicker 0.1s infinite alternate;
  background: linear-gradient(45deg, transparent 30%, rgba(0, 230, 255, 0.1) 50%, transparent 70%);
}

.status-indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  position: relative;
}

.status-indicator::after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border-radius: 50%;
  animation: neonPulse 2s infinite;
}

.status-online {
  background: var(--neon-green);
}

.status-offline {
  background: var(--neon-orange);
}

.status-error {
  background: var(--neon-pink);
}

.chat-bubble-user {
  background: linear-gradient(135deg, rgba(0, 230, 255, 0.2), rgba(196, 0, 255, 0.2)) !important;
  border: 1px solid rgba(0, 230, 255, 0.5) !important;
  border-radius: 18px 18px 4px 18px !important;
}

.chat-bubble-ai {
  background: linear-gradient(135deg, rgba(0, 255, 132, 0.1), rgba(255, 102, 0, 0.1)) !important;
  border: 1px solid rgba(0, 255, 132, 0.3) !important;
  border-radius: 18px 18px 18px 4px !important;
}

.voice-visualizer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
}

.voice-bar {
  width: 3px;
  background: var(--neon-green);
  border-radius: 2px;
  animation: dataStream 1s infinite;
}

.voice-bar:nth-child(1) { height: 10px; animation-delay: 0s; }
.voice-bar:nth-child(2) { height: 20px; animation-delay: 0.1s; }
.voice-bar:nth-child(3) { height: 15px; animation-delay: 0.2s; }
.voice-bar:nth-child(4) { height: 25px; animation-delay: 0.3s; }
.voice-bar:nth-child(5) { height: 12px; animation-delay: 0.4s; }

.loading-dots {
  display: inline-flex;
  gap: 4px;
}

.loading-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--neon-blue);
  animation: neonPulse 1.4s infinite ease-in-out;
}

.loading-dot:nth-child(1) { animation-delay: -0.32s; }
.loading-dot:nth-child(2) { animation-delay: -0.16s; }
.loading-dot:nth-child(3) { animation-delay: 0s; }

.modal-backdrop {
  background: rgba(0, 0, 0, 0.8) !important;
  backdrop-filter: blur(5px);
}

.modal-content {
  background: var(--dark-card) !important;
  border: 2px solid var(--neon-blue) !important;
  border-radius: 16px !important;
  box-shadow: 0 0 50px rgba(0, 230, 255, 0.3) !important;
}

.futuristic-input {
  background: rgba(17, 17, 17, 0.8) !important;
  border: 1px solid rgba(0, 230, 255, 0.3) !important;
  border-radius: 8px !important;
  color: #e0e0e0 !important;
  font-family: var(--body-font) !important;
  transition: all 0.3s ease !important;
}

.futuristic-input:focus {
  outline: none !important;
  border-color: var(--neon-blue) !important;
  box-shadow: 0 0 20px rgba(0, 230, 255, 0.3) !important;
}

.futuristic-textarea {
  background: rgba(17, 17, 17, 0.8) !important;
  border: 1px solid rgba(0, 230, 255, 0.3) !important;
  border-radius: 8px !important;
  color: #e0e0e0 !important;
  font-family: var(--body-font) !important;
  resize: vertical !important;
  transition: all 0.3s ease !important;
}

.futuristic-textarea:focus {
  outline: none !important;
  border-color: var(--neon-blue) !important;
  box-shadow: 0 0 20px rgba(0, 230, 255, 0.3) !important;
}

.command-terminal {
  background: rgba(0, 0, 0, 0.9) !important;
  border: 1px solid var(--neon-green) !important;
  border-radius: 8px !important;
  font-family: 'Courier New', monospace !important;
  color: var(--neon-green) !important;
}

.system-grid {
  display: grid;
  gap: 1px;
  background: rgba(0, 230, 255, 0.1);
}

.grid-cell {
  background: var(--dark-bg);
  aspect-ratio: 1;
  border: 1px solid rgba(0, 230, 255, 0.2);
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--dark-bg);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, var(--neon-blue), var(--neon-purple));
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, var(--neon-purple), var(--neon-blue));
}

/* Responsive Design */
@media (max-width: 768px) {
  .futuristic-card {
    margin: 8px;
  }
  
  .neon-button {
    font-size: 14px;
    padding: 8px 16px;
  }
}
